<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基本用法1">基本用法</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-loadingbar加载进度条1" class="vue-doc-element">LoadingBar 加载进度条</h1>
<p class="vue-doc-element">全局创建一个显示页面加载、异步请求、文件上传等的加载进度条。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
        </div>


        <div class="vue-doc-content">
          <p class="vue-doc-element">LoadingBar 只会在全局创建一个，因此在任何位置调用的方法都会控制这同一个组件。主要使用场景是路由切换和Ajax，因为这两者都不能拿到精确的进度，LoadingBar 会模拟进度，当然也可以通过update()方法来传入一个精确的进度，比如在文件上传时会很有用，具体见API。</p>
<h3 id="vue-doc-在路由中使用1" class="vue-doc-element">在路由中使用</h3>
<pre class="vue-doc-element"><code class="lang-js"><span class="hljs-comment">// 部分代码省略</span>
<span class="hljs-keyword">import</span> iView <span class="hljs-keyword">from</span> <span class="hljs-string">'iview'</span>;
Vue.use(iView);

router.beforeEach(<span class="hljs-function">(<span class="hljs-params">to, <span class="hljs-keyword">from</span>, next</span>) =&gt;</span> {
    iView.LoadingBar.start();
    next();
});

router.afterEach(<span class="hljs-function"><span class="hljs-params">route</span> =&gt;</span> {
    iView.LoadingBar.finish();
});
</code></pre>
<h3 id="vue-doc-在异步请求中使用2" class="vue-doc-element">在异步请求中使用</h3>
<pre class="vue-doc-element"><code class="lang-js"><span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-comment">// 以jQuery的Ajax为例，部分代码省略</span>
<span class="hljs-keyword">import</span> $ <span class="hljs-keyword">from</span> <span class="hljs-string">'jquery'</span>;
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
        getData () {
            <span class="hljs-keyword">this</span>.$Loading.start();
            $.ajax({
                <span class="hljs-attr">url</span>: <span class="hljs-string">'/api/someurl'</span>,
                <span class="hljs-attr">type</span>: <span class="hljs-string">'get'</span>,
                <span class="hljs-attr">success</span>: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
                    <span class="hljs-keyword">this</span>.$Loading.finish();
                }
                error: <span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
                    <span class="hljs-keyword">this</span>.$Loading.error();
                }
            });
        }
    }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-loadingbarinstance1_1" class="vue-doc-element">LoadingBar instance</h3>
<p class="vue-doc-element">通过直接调用以下方法来使用组件：</p>
<ul class="vue-doc-element">
<li><code class="vue-doc-element">this.$Loading.start()</code></li>
<li><code class="vue-doc-element">this.$Loading.finish()</code></li>
<li><code class="vue-doc-element">this.$Loading.error()</code></li>
<li><code class="vue-doc-element">this.$Loading.update(percent)</code>
以上方法隐式的创建及维护Vue组件。函数及参数说明如下：</li>
</ul>
<table class="vue-doc-element">
<thead>
<tr>
<th>函数名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>start</td>
<td>开始从 0 显示进度条，并自动加载进度</td>
<td>无</td>
</tr>
<tr>
<td>finish</td>
<td>结束进度条，自动补全剩余进度</td>
<td>无</td>
</tr>
<tr>
<td>error</td>
<td>以错误的类型结束进度条，自动补全剩余进度</td>
<td>无</td>
</tr>
<tr>
<td>update</td>
<td>精确加载到指定的进度</td>
<td>percent，指定的进度百分比</td>
</tr>
</tbody>
</table>
<p class="vue-doc-element">另外提供了全局配置和全局销毁的方法：</p>
<ul class="vue-doc-element">
<li><code class="vue-doc-element">this.$Loading.config(options)</code></li>
<li><code class="vue-doc-element">this.$Loading.destroy()</code></li>
</ul>
<pre class="vue-doc-element"><code class="lang-js"><span class="hljs-keyword">this</span>.$Loading.<span class="hljs-built_in">config</span>({
    color: <span class="hljs-string">'#5cb85c'</span>,
    failedColor: <span class="hljs-string">'#f0ad4e'</span>,
    <span class="hljs-built_in">height</span>: <span class="hljs-number">5</span>
});
</code></pre>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>color</td>
<td>进度条的颜色，默认为 iView 主色</td>
<td>String</td>
<td>primary</td>
</tr>
<tr>
<td>failedColor</td>
<td>失败时的进度条颜色，默认为 iView 主色</td>
<td>String</td>
<td>error</td>
</tr>
<tr>
<td>height</td>
<td>进度条高度，单位 px</td>
<td>Number</td>
<td>2</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


